.calendar {
    border-collapse: separate;
    border-spacing: 12px 12px;
    &__item {
        position: relative;
        border: 1px solid black;
        width: 76px;
        height: 76px;
        border-radius: 50%;

        &_active {
            border: 2px solid red;
        }
    }

    &__item-link {
        display: block;
        width: 100%;
        height: 100%;
        color: inherit;
        text-decoration: none;
    }

    &__item-dayweek, &__item-day {
        position: absolute;
        line-height: 100%;
    }

    &__item-dayweek {
        left: -8%;
        top: -10%;
    }

    &__item-day {
        font-size: 2.5rem; // 40px

        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);       
    }
}