.add-todo {
    &__form {
        padding: 25px 20px;
        background-color: darkgray;
        width: 30%;
        display: flex;
        flex-direction: column;
        border: 1px solid black;
        background-color: rgb(103, 143, 143);
    }
    &__label {
        display: flex;
        flex-direction: column;

        margin-bottom: 20px;
    }

    &__label-text {
        color: #ffff;
    }

    &__input {
        background: none;
        border: none;
        outline: none;
    }

    &__textarea {
        background: none;
        border: none;
        outline: none;
        resize: none;
        height: 200px;
    }

}